import interfaces from '../../utils/interfaces.js';
import app from "../../App.vue";
export default {
	data() {
		return {
			user_info: '',
			token: '',
			nickName: ''
		}
	},
	onShow() {
		this.user_info = app.globalData.user_info;
		if (this.user_info) {
			this.token = app.globalData.user_info.token;
			this.nickName = app.globalData.user_info.nickName;
		} else {
			wx.reLaunch({
				url: '/pages/index/index',
			})
		}
	},
	methods: {
		//表单提交事件
		  formSubmit(e) {
		    var data = e.detail.value;
		    if (data.content=='') {
		      wx.showToast({
		        title: '请输入反馈内容',
		        icon: "none"
		      });
		      return;
		    }
		
		    this.request({
				url: interfaces.feedbackAdd,
				header:{
					token:this.token
				},
				data: {
					content: data.content,
					nickName: this.nickName
				},
				method: 'POST',
				success: res => {
					console.log(res);
					if (res.code == 200) {
						wx.showToast({
							title: res.msg,
							icon: 'none'
						}) 
						setTimeout(function () {
							wx.navigateBack({
								delta: 1
							})
						}, 1500);
					} else {
						app.isLogin(res.code, res.msg);
					}
				}
		    });
		},
	}
}
